import VerifyCode from '../../components/VerifyCode';
import callingCode from '../../js/callingCode';

export default {
    name: 'Post',
    components: {
        Validator,
        VerifyCode
    },
    filters: {
        formatTime(value) {
            return moment.utc(value).local().format('YYYY-MM-DD HH:mm');
        }
    },
    data() {

        var message = {
            en: {
                phone: 'Phone Number',
                password: 'Password',
                verifyCode: 'Verify Code',
                require: 'Required',
                phoneMin: 'The phone number cannot be less than 8 digits',
                phoneMax: 'The phone number cannot exceed 11 digits',
                verifyReg: 'Please input 6-digit verification code',
                passwordReg: 'The password cannot be less than 6 digits and must contain a number and letter',
                termsTextTip: "I agree to ",
                termsText:"Tweebaa's terms and policy",
                submit: 'Signup',
                isRequesting: 'Submitting',
                success: 'Success',
                tweebaa: 'Tweebaa',
                downloadApp: 'Download Tweebaa App Get T-beans',
                platform: 'Tweebaa is the world\'s 1st multi-dimensional earning commerce and value-exchanging social networking platform allowing everyone to transform their knowledge, skills, interests, and experiences into creative products and discover their hidden values.',
            },
            zh: {
                phone: '',
                password: '',
                verifyCode: '',
                require: '',
                phoneMin: '8',
                phoneMax: '11',
                verifyReg: '6',
                passwordReg: '6',
                termsTextTip: "",
                termsText:"",
                submit: '',
                isRequesting: '',
                success: '',
                tweebaa: '',
                downloadApp: 'AppT',
                platform: '',
            },
        };
        return {
            currentCalling: null,
            callingCode: callingCode,
            form: {
                registerCountryCode: 'CN',
                countryCallingCode: '86',
                password: '',
                phoneNumber: '',
                verifyCode: '',
                userName: '',
                inviter: this.$util.getUrlParam('shareid'),
            },
            rules: {
                phoneNumber: {
                    required: true,
                    requiredTip: message[this.$util.language].require,
                    minlength: 8,
                    minlengthTip: message[this.$util.language].phoneMin,
                    maxlength: 11,
                    maxlengthTip: message[this.$util.language].phoneMax
                },
                verifyCode: {
                    required: true,
                    requiredTip: message[this.$util.language].require,
                    minlength: 6,
                    maxlength: 6,
                    minlengthTip: message[this.$util.language].verifyReg,
                    maxlengthTip: message[this.$util.language].verifyReg
                },
                password: {
                    required: true,
                    requiredTip: message[this.$util.language].require,
                    pattern: /^(?=.*\d)(?=.*[a-zA-Z])[\da-zA-Z~!@#\$%\^&\*\(\)_+]{6,20}$/,
                    patternTip: message[this.$util.language].passwordReg
                }
            },
            isRequesting: false,
            isSuccess: false,
            isCheckbox: true,
            messages: message
        };
    },
    mounted() {

        // $(document).delegate('.m-form .input', 'focus', (e) => {
        //     let $label = $(e.target).closest('.col-input').siblings('.col-label');
        //     if ($label.hasClass('focus') === false) {
        //         $label.addClass('focus');
        //     }
        // });
        //
        // $(document).delegate('.m-form .input', 'blur', (e) => {
        //     let $input = $(e.target);
        //     if ($input.val() === '') {
        //         let $label = $input.closest('.col-input').siblings('.col-label');
        //         $label.removeClass('focus');
        //     }
        // });

        this.shareid = this.$util.getUrlParam('shareid');
    },
    created() {
        var tmpCountryCode;
        if (this.$util.language === 'zh') {
            tmpCountryCode = 'CN';
        } else {
            tmpCountryCode = 'CA';
        }
        this.setCountry(tmpCountryCode);

        this.getCountryCode();
    },
    methods: {
        setCountry(countryCode) {
            $.map(callingCode, (item, index) => {
                if (item.code === countryCode) {
                    this.currentCalling = item;
                }
            });
        },
        getCountryCode() {
            var that = this;
            axios.get("https://api.ipdata.co/?api-key=78e1295abccb1616027b85a6bcb612d286aef4cdb397a2c091fd861a") // eric.guo77@gmail.com code
                .then(function (response) {
                    that.form.registerCountryCode = response.data.country_code;
                    that.setCountry(that.form.registerCountryCode);
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        goTerm($event) {
            $event.stopPropagation();
            window.location.href = './term.html';
        },
        toggleCheckbox() {
            this.isCheckbox = !this.isCheckbox;
        },
        submit() {
            if (this.rules.$checkAll() === false) {
                return;
            }

            var param = $.extend({}, this.form);

            param.userName = 'tw' + Date.parse(new Date());
            param.registerCountryCode = this.currentCalling.code;
            param.countryCallingCode = this.currentCalling.number;

            var that = this;

            that.isRequesting = true;

            let url = this.$util.getUserCenterUrl() + '/api/v1/uas/web/register';

            this.$util.getAxois(url, "POST", param).then((response) => {
                that.isRequesting = false;
                var rep = response.data;
                if (rep.code === 0) {
                    that.isSuccess = true;
                } else {
                    that.isSuccess = false;
                }
            });
        }
    }
}
