<template>
    <div class="comp-verify-sms">
        <span class="text" v-if="isLoading === true">{{messages[language].sending}}</span>
        <a href="javascript:;"
           class="link"
           id="verifySend"
           @click="openSlide"
           v-if="isLoading === false && hasBeginCount === false">{{messages[language].start}}</a>
        <span class="text" v-if="isLoading === false && hasBeginCount === true">{{countSeconds}}</span>
    </div>
</template>

<script type="text/babel">

    const COUNT_LIMIT = 60;

    export default {
        props: {
            phoneNumber: {
                type: String,
            },
            countryCallingCode: {
                type: String
            },
            onSuccess: {
                type: Function,
                default() {

                }
            }
        },
        components: {},
        watch: {},
        data() {
            var message = {
                en: {
                    start: 'Send'
                },
                zh: {
                    sending: '',
                    start: '',
                    repeat: '',
                    retry: 's'
                }
            };
            return {
                verifyToken: '',
                verifyAuthenticate: '',
                isLoading: false,
                hasBeginCount: false,
                countSeconds: COUNT_LIMIT + message[this.$util.language].retry,
                number: COUNT_LIMIT,
                timer: null,
                code: '',
                codeBtn: message[this.$util.language].start,
                isRequesting: false,
                messages: message
            }
        },
        mounted() {
            this.initYP();
        },
        created() {

        },
        methods: {
            openSlide() {
                $('.yp-riddler-button').trigger('click');
            },
            resetYP: function() {
                this.verifyToken = '';
                this.verifyAuthenticate = '';
                this.initYP();
            },
            initYP: function() {
                var that = this;
                new YpRiddler({
                    expired: 10,
                    mode: 'dialog',
                    winWidth: $(window).width() * 0.9,
                    lang: this.$util.language === 'zh' ?"zh-cn":"en",
                    // langPack: LANG_OTHER, // , lang
                    container: document.getElementById('cbox'),
                    appId: '88ea1d6327b14ce4a629c501245f9bea' ,
                    version: 'v1',
                    onError: function (param) {
                        if (!param.code) {
                            console.error('');
                        }
                        else if (parseInt(param.code / 100) == 5) {
                            // 
                            console.error('');
                        }
                        else if (param.code == 429) {
                            console.warn('');
                        }
                        else if (param.code == 403) {
                            console.warn('');
                        }
                        else if (param.code == 400) {
                            console.warn('');
                        }
                        // 
                        console.error(':' + param.code)
                        alert('' + param.code);
                        // api.toast({
                        //     msg: ':' + param.code,
                        //     duration: 2000,
                        //     location: 'middle'
                        // });
                    },
                    onSuccess: function (validInfo, close, useDefaultSuccess) {
                        // 
                        console.log('! token=' + validInfo.token + ', authenticate=' + validInfo.authenticate)
                        // 
                        that.verifyAuthenticate = validInfo.authenticate;
                        that.verifyToken = validInfo.token;
                        useDefaultSuccess(true);
                        close();

                        setTimeout(function () {
                            that.send();
                        }, 200);
                    },
                    onFail: function (code, msg, retry) {
                        // 
                        alert('' + msg + ' code: ' + code);
                        retry()
                    },
                    beforeStart: function (next) {
                        //console.log('')
                        next()
                    }
                })
            },
            send() {
                this.isLoading = true;

                let paramStr = {
                    "authenticate": this.verifyAuthenticate,
                    "token": this.verifyToken,
                    "cellphone": this.phoneNumber,
                    "countryCode": this.countryCallingCode,
                    "lang": this.$util.language
                };

                let url = this.$util.getUserCenterUrl() + '/api/v1/uas/web/smsv2';

                this.$util.getAxois(url, "POST", paramStr).then((response) => {
                    this.isLoading = false;
                    var rep = response.data;
                    if (rep.code === 0) {
                        this.hasBeginCount = true;
                        this.countSeconds = this.number + this.messages[this.$util.language].retry;
                        clearInterval(this.timer);
                        this.onSuccess();
                        this.timer = setInterval(() => {
                            if (this.number > 1) {
                                this.number--;
                                this.countSeconds = this.number + this.messages[this.$util.language].retry;
                            } else {
                                clearInterval(this.timer);
                                this.hasBeginCount = false;
                                this.codeBtn = this.messages[this.$util.language].repeat;
                                this.number = COUNT_LIMIT;
                                this.code = '';
                            }
                        }, 1000);

                    } else {
                        console.log(rep.message);
                        this.resetYP();
                    }

                });


            }
        },
        beforeDestroy() {
            window.clearInterval(this.timer);
        }
    }
</script>

<style lang="less">
    @color-blue: #4578F3;
    .comp-verify-sms {
        box-sizing: border-box;
        display: inline-block;
        width: 10rem;
        height: 4.8rem;
        line-height: 4.8rem;
        text-align: center;
        border: 1px solid #f0f0f0;
        border-left: 0 none;
        .link {
            font-size: 13px;
            color: @color-blue;
        }
        .text {
            font-size: 13px;
            color: #999;
        }
    }
</style>
