import './Toast.less';

import Vue from 'vue';

const TIMEOUT = 2500;
const TIMEOUT_ERROR = 4000;

let $element = null;

let vm = new Vue({
    template: `
        <div class="comp-toast" :class="['comp-toast--' + type, {'comp-toast-top': top}]">
            <div class="toast-wrapper">
                <div class="close" @click="hide()" v-if="type === 'error'">
                    <span class="text"></span>
                </div>
                <span class="text" v-html="content"></span>
            </div>
        </div>
    `,
    components: {

    },
    created() {

    },
    mounted() {
        this.$module = $(this.$refs.module);

    },
    data() {
        return {
            top: false,
            hasInit: false,
            timeId: null,
            $module: null,
            isShow: false,
            classObj: {

            },
            transition: 'fade',
            type: 'warning',
            content: '',
            timeout: TIMEOUT,
            callback() {}
        };
    },
    methods: {
        show() {
            if (this.hasInit === false) {
                let elem = document.createElement('div');
                document.body.appendChild(elem);
                this.hasInit = true;
                vm.$mount(elem);
                $element = $(this.$el);
            }
            this.isShow = true;
            setTimeout(() => {
                $element.addClass('slide-fade-enter-active');
            }, 0);

            if (this.timeId) {
                window.clearTimeout(this.timeId);
            }
            // 
            if (this.timeout === -1) {
                return;
            }
            this.timeId = setTimeout(() => {
                this.hide();
                if (typeof this.callback === 'function') {
                    this.callback();
                }
            }, this.timeout);
        },
        hide() {
            this.isShow = false;
            $element.removeClass('slide-fade-enter-active');
        }
    }
});

let toast = function (msgOrConfig, callback, timeout, top) {

    let config = {};

    if (typeof msgOrConfig === 'string') {
        config.content = msgOrConfig;
        config.callback = callback;
        config.timeout = timeout;
    } else {
        config = msgOrConfig;
        if (config.type === 'error' && !config.timeout) {
            config.timeout = TIMEOUT_ERROR;
        }
    }

    let option = $.extend({}, {
        transition: config.transition || 'fade',
        type: config.type || 'warning',
        content: config.content,
        timeout: config.timeout || TIMEOUT,
        callback: config.callback,
        top: config.top
    });

    $.map(option, (value, key) => {
        vm[key] = value;
    });

    vm.show();

};

window.toast = toast;

window.success = function (msgOrConfig, callback, timeout) {
    let config = {};
    if (typeof msgOrConfig === 'string') {
        config.type = 'success';
        config.content = msgOrConfig;
        config.callback = callback;
        config.timeout = timeout;
    } else {
        config = $.extend({
            type: 'success'
        }, msgOrConfig);
    }
    toast(config);
};

window.warning = function (msgOrConfig, callback, timeout, top) {
    let config = {};
    if (typeof msgOrConfig === 'string') {
        config.type = 'warning';
        config.content = msgOrConfig;
        config.callback = callback;
        config.timeout = timeout;
        config.top = top;
    } else {
        config = $.extend({
            type: 'warning'
        }, msgOrConfig);
    }
    toast(config);
};

window.error = function (msgOrConfig, callback, timeout) {
    let config = {};
    if (typeof msgOrConfig === 'string') {
        config.type = 'error';
        config.content = msgOrConfig;
        config.callback = callback;
        config.timeout = timeout || TIMEOUT_ERROR;
    } else {
        config = $.extend({
            type: 'error'
        }, msgOrConfig);
    }
    toast(config);
};

window.hideToast = () => {
    vm.hide();
};

/**
 * confirm
 * @param config
 * @returns {*}
 */
export default toast;
